import { Component, OnInit } from '@angular/core';
import { ReportService, IAnswer } from '../../@core/data/report.service';
@Component({
  selector: 'ngx-dashboard',
  styleUrls: ['./dashboard.component.scss'],
  templateUrl: './dashboard.component.html',
})
export class DashboardComponent implements OnInit {
  answers: IAnswer[] = [];
  totalConversation: number = 0;
  totalSucessConversation: number = 0;
  totalFailureConversation: number = 0;
  totalTodaysConversation: number = 0;
  constructor(private service: ReportService,
  ) {

  }

  ngOnInit() {
    this.getConversationList();
  }

  getConversationList() {
    this.service.getAnswers().subscribe(resp => {
      resp.forEach(x => {
        this.answers.push({
          compiledAnswer: x.compiledAnswer,
          confident: x.confident,
          question: x.question,
          rawAnswer: x.rawAnswer,
          timeStamp: new Date(x.timestamp).toDateString(),
        });
      });
      this.totalConversation = this.answers.length;

      this.totalSucessConversation = this.answers.filter(function (x) {
        return x.rawAnswer !== 'No Match';
      }).length;
      this.totalFailureConversation = this.answers.filter(function (x) {
        return x.rawAnswer === 'No Match';
      }).length;
      this.totalTodaysConversation = this.answers.filter(function (x) {
        const dateObj = new Date();
        const month = dateObj.getUTCMonth() + 1; // months from 1-12
        const day = dateObj.getUTCDate();
        const year = dateObj.getUTCFullYear();

        const localDate = year + '/' + month + '/' + day;

        const dateObjServer = new Date(x.timeStamp);
        const monthServer = dateObjServer.getUTCMonth() + 1; // months from 1-12
        const dayServer = dateObjServer.getDate();
        const yearServer = dateObjServer.getUTCFullYear();

        const serverDate = yearServer + '/' + monthServer + '/' + dayServer;
        return localDate === serverDate;
      }).length;

    });
  }
}
